.Header {
  width: 100%;
  height: 500px;
  margin: 0;
  color: #fff;
  font-family: 'Work Sans';
  background:-webkit-gradient(linear,left bottom, left top,from(rgba(29, 29, 29, 0.616)),to(rgba(255,0,150,0.4))),url("header.jpg");
  background:-webkit-linear-gradient(bottom,rgba(29, 29, 29, 0.616),rgba(255,0,150,0.4)),url("header.jpg");
  background:-o-linear-gradient(bottom,rgba(29, 29, 29, 0.616),rgba(255,0,150,0.4)),url("header.jpg");
  background:linear-gradient(0deg,rgba(29, 29, 29, 0.616),rgba(255,0,150,0.4)),url("header.jpg");
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.slogan {
  font-size: 18px;
  text-align: center;
}

.welcome2 {
  margin-top: 30px;
  width: 50%;
  height: 1%;
  padding: 30px;
  border-radius: .6em;
  font-family: -apple-system,BlinkMacSystemFont,sans-serif,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Arial;
  font-size: 40px;
  -webkit-transition: all .6s cubic-bezier(0,.5,.51,1);
  -o-transition: all .6s cubic-bezier(0,.5,.51,1);
  transition: all .6s cubic-bezier(0,.5,.51,1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.welcome2 p {
  font-family: 'Work Sans';
  font-weight: 400;
  margin-left: 10px;
}

.welcome2 {
  -webkit-animation-name: grow;
          animation-name: grow;
  -webkit-animation-duration: 600ms;
          animation-duration: 600ms;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: cubic-bezier();
          animation-timing-function: cubic-bezier();
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  /* transform: rotate(3deg); */
   /* transform: rotate(0.3rad);/ */
   /* transform: rotate(3grad); */ 
   /* transform: rotate(.03turn);  */
}

@-webkit-keyframes grow {
  from {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes grow {
  from {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


