
.sep-1 {
  height: 5px;
  width: 100%;
  background: -webkit-linear-gradient(135deg, rgba(253,240,147,1), rgb(253,117,81), rgba(253,240,147,1), rgb(253,117,81));
  background: -o-linear-gradient(135deg, rgba(253,240,147,1), rgb(253,117,81), rgba(253,240,147,1), rgb(253,117,81));
  background: linear-gradient(-45deg, rgba(253,240,147,1), rgb(253,117,81), rgba(253,240,147,1), rgb(253,117,81));
	background-size: 400% 400%;
	-webkit-animation: Gradient 10s ease infinite;
	animation: Gradient 10s ease infinite;
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}


